.carrousel {
    position: relative;
    height: 415px;
}

.carrousel__img {
    height: 415px;
    width: 1240px;
    object-fit: cover;
    border-radius: 25px;
}

.fa-chevron-right {
    position: absolute;
    font-size: 79px;
    right: 25px;
    top: 160px;
    color: #fff;
}

.fa-chevron-left {
    position: absolute;
    font-size: 79px;
    left: 25px;
    top: 160px;
    color: #fff;
}

.carrousel__img__number {
    position: absolute;
    bottom: 25px;
    left: 50%;
    font-size: 18px;
    font-weight: 500;
    color: #fff;
}

@media (max-width: 375px) {
    .carrousel {
        height: 255px;
    }
    .carrousel__img {
        height: 255px;
        width: 335px;
        border-radius: 10px;
    }
    .fa-chevron-right {
        font-size: 24px;
        right: 25px;
        top: 50%;
        margin-top: -10px;
    }

    .fa-chevron-left {
        position: absolute;
        font-size: 24px;
        left: 25px;
        top: 50%;
        margin-top: -10px;
    }
    .carrousel__img__number {
        display: none;
    }
}
